.App {
    text-align: center;
    background-color: #e3f4fe;
    font-family: monospace;
}
.container {
    font-family: monospace;
    background-image: linear-gradient(#19c3fb, #1c9cf6);
    min-width: 500px;
    min-height: 350px;
    text-align: center;
    align-content: center;
    align-items: center;
}
.div3{
    box-sizing: content-box;
    width: 150px;
    height: 220px;
    text-align: center;
    display: inline-block;
    padding:5px;
}
#button{
    position: relative;
    width: 150px;
    height: 220px;
    border: none;
    outline: none;
    text-align: center; 
    font-size: 18px;
}
#removebutton{
    position: relative;
    top: 20px;
    border: none;
    outline: none;
}