.App {
    text-align: center;
    background-color: #e3f4fe;
    font-family: monospace;
}
.container {
    font-family: monospace;
    background-image: linear-gradient(#19c3fb, #1c9cf6);
    min-width: 500px;
    min-height: 600px;
    text-align: center;
}
.single-day {
    border-radius: 10px;
    box-sizing: content-box;
    width: 150px;
    height: 220px;
    text-align: center;
    display: inline-block;
}
#addbutton{
    border: none;
    outline: none;
    position: relative;
    left: 450px;
    top:20px
}
.small-image {
    width: 170px;
    height: 100px;
    margin-bottom: 2px;
}